import axios from 'axios';
import React, {Component, Fragment} from 'react';
import {Link, withRouter} from 'react-router-dom';
import {CSSTransition, TransitionGroup} from 'react-transition-group';
import '../../../assets/Common.css';
import './Infomation.css';

import {
    Card,
    CardTitle,
    CardBody,
    Col,
    Row,
    Table,
    Progress
} from 'reactstrap';
import {Button, Modal, ModalHeader, ModalBody, ModalFooter} from 'reactstrap';

class BasicInfomantion extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userData: [],
            sortAscending: true,
            sortBy: 'level',
            sortNameAscending: true,
            downloading: false,
            download_data1: [],
            complete_download: false,
            downloadModalVisible: false
        };
    }

    componentDidMount() {
        this.loadData()
    }

    componentDidUpdate() {}

    CustomCard = ({label, user}) => {

        var level = 'success';

        if (user.hrData === '미측정' || user.spo2Data === '미측정' || user.hrData < 0 || user.spo2Data < 0) {
            level = 'secondary';
        }

        if ((user.hrData >= 0 && user.hrData <= 70) || (user.spo2Data >= 0 && user.spo2Data <= 94)) {
            level = 'warning';
        }

        if ((user.hrData >= 0 && (user.hrData <= 60 || user.hrData > 110)) || (user.spo2Data >= 0 && user.spo2Data <= 90)) {
            level = 'danger';
        }

        //console.log('level : ' + level);

        return (
            <Card color={level} className='horizontal-card'>
                <CardBody className="mb-0 p-1">
                    <p className="text-white mb-0">{label}</p>
                </CardBody>
                <CustomModal selectedUser={user}/>
            </Card>
        );
    };

    CustomProgressLabels = (props) => {
        var danger = 0;
        var warning = 0;
        var good = 0;
        var noData = 0;

        return (
            <div>
                {
                    this
                        .state
                        .userData
                        .map((item, index) => {
                            var level = 'success';

                            if (item.hrData === '미측정' || item.spo2Data === '미측정' || item.hrData < 0 || item.spo2Data < 0) {
                                level = 'secondary';
                            }

                            if ((item.hrData >= 0 && item.hrData <= 70) || (item.spo2Data >= 0 && item.spo2Data <= 94)) {
                                level = 'warning';
                            }

                            if ((item.hrData >= 0 && (item.hrData <= 60 || item.hrData > 110)) || (item.spo2Data >= 0 && item.spo2Data <= 90)) {
                                level = 'danger';
                            }

                            if (level === 'success') 
                                good++;
                            else if (level === 'secondary') 
                                noData++;
                            else if (level === 'warning') 
                                warning++;
                            else if (level === 'danger') 
                                danger++;
                            }
                        )
                }
                <p className='mb-0'>전체 사용자 건강 상태(전체 {good + danger + warning + noData}명)</p>
                <Progress className="progress-bar-rounded" multi="multi">
                    <Progress
                        animated="animated"
                        bar="bar"
                        color="success"
                        value={good / (good + danger + warning + noData) * 100}>양호 {Math.round(good / (good + danger + warning + noData) * 100)}% ({good}명)</Progress>
                    <Progress
                        className="text-white"
                        animated="animated"
                        bar="bar"
                        color="warning"
                        value={warning / (good + danger + warning + noData) * 100}>주의 {Math.round(warning / (good + danger + warning + noData) * 100)}% ({warning}명)</Progress>
                    <Progress
                        animated="animated"
                        bar="bar"
                        color="danger"
                        value={danger / (good + danger + warning + noData) * 100}>위험 {Math.round(danger / (good + danger + warning + noData) * 100)}% ({danger}명)</Progress>
                    <Progress
                        animated="animated"
                        bar="bar"
                        color="secondary"
                        value={noData / (good + danger + warning + noData) * 100}>미측정 {Math.round(noData / (good + danger + warning + noData) * 100)}% ({noData}명)</Progress>
                </Progress>
                <div className='boader-dark-gray mt-2'></div>
            </div>
        );
    };

    async loadData() {
        axios
            .get('https://api.ssirc.dev/api/User/')
            .then(userResponse => {
                const usersWithAdditionalData = [];

                Promise
                    .all(userResponse.data.map(user => {

                        const userWithAdditionalData = {
                            ...user,
                            hrData: '미측정',
                            spo2Data: '미측정',
                            abnormalHrData: '미측정',
                            stressData: '미측정',
                            depressData: '미측정',
                            stepData: '미측정',
                            recoveryData: '미측정'
                        };
                        usersWithAdditionalData.push(userWithAdditionalData);

                    }))
                    .then(() => {
                        this.setState({userData: usersWithAdditionalData});
                        this.updatePagination(usersWithAdditionalData);
                    });
            })
            .catch(userError => console.error('Error fetching userData', userError));
    }

    updatePagination(data) {
        console.log('data.length : ' + data.length);
        for (let i = 1; i < data.length / 10 + 1; i++) {

            console.log('currentPage : ' + i);

            axios
                .get('https://test.ssirc.dev/Userprofile/paged/?page=' + i)
                .then(pageResponse => {
                    Promise
                        .all(pageResponse.data.results.map(user => {
                            data.forEach(element => {
                                if (element.User_id == user.User_id) {

                                    var lastHrData = user.Hr[user.Hr.length - 1];
                                    var lastSpo2Data = user.Spo2[user.Spo2.length - 1];
                                    var lastAbnormalHrData = user.Stress[user.Stress.length - 1];
                                    var lastStressData = user.Depress[user.Depress.length - 1];
                                    var lastDepressData = user.Abnormal_hr[user.Abnormal_hr.length - 1];
                                    var lastStepData = user.Step[user.Step.length - 1];
                                    var lastRecoveryData = '';

                                    lastHrData = lastHrData
                                        ? lastHrData.Hr
                                        : '미측정';
                                    lastSpo2Data = lastSpo2Data
                                        ? lastSpo2Data.Spo2
                                        : '미측정';
                                    lastAbnormalHrData = lastAbnormalHrData
                                        ? (lastAbnormalHrData.Abnormal_hr === true)
                                            ? 'Y'
                                            : 'N'
                                        : '미측정';
                                    lastStressData = lastStressData
                                        ? (lastStressData.Stress === true)
                                            ? 'Y'
                                            : 'N'
                                        : '미측정';
                                    lastDepressData = lastDepressData
                                        ? lastDepressData.Depress
                                        : '미측정';
                                    lastStepData = lastStepData
                                        ? lastStepData.Step
                                        : '미측정';
                                    lastRecoveryData = lastRecoveryData
                                        ? lastRecoveryData.Recovery
                                        : '미측정';

                                    if (lastDepressData === 3) 
                                        lastDepressData = "심각";
                                    else if (lastDepressData === 2) 
                                        lastDepressData = "중증도";
                                    else if (lastDepressData === 1) 
                                        lastDepressData = "경도";
                                    else if (lastDepressData === 0) 
                                        lastDepressData = "정상";
                                    
                                    if (lastRecoveryData === 2) 
                                        lastRecoveryData = "높음";
                                    else if (lastRecoveryData === 1) 
                                        lastRecoveryData = "보통";
                                    else if (lastRecoveryData === 0) 
                                        lastRecoveryData = "낮음";
                                    
                                    user.Gps_location = user.Gps_location
                                        ? user.Gps_location
                                        : 'no-idea';

                                    element.hrData = lastHrData;
                                    element.spo2Data = lastSpo2Data;
                                    element.abnormalHrData = lastAbnormalHrData;
                                    element.stressData = lastStressData;
                                    element.depressData = lastDepressData;
                                    element.stepData = lastStepData;
                                    element.recoveryData = lastRecoveryData;
                                }
                            });

                        }))
                        .then(() => {
                            this.setState({userData: data});

                        })
                        . finally(() => {});

                })
                .catch(userError => console.error('Error fetching userData', userError));
        }
    }

    handleSortButtonClick = () => {
        // Toggle the sorting order when the button is clicked
        this.setState((prevState) => ({
            sortAscending: !prevState.sortAscending,
            sortBy: 'level'
        }));
    };

    handleSortByName = () => {
        // Set the sorting field to 'Name'
        this.setState((prevState) => ({
            sortNameAscending: !prevState.sortNameAscending,
            sortBy: 'Name'
        }));
    };

    buttonReset = () => {
        this.setState({downloadModalVisible: false});
        console.log(
            'downloadModalVisibl(buttonReset) : ' + this.state.downloadModalVisible
        );
    }

    handleDownloadButtonClick = () => {

        // this.setState((prevState) => ({     downloadModalVisible:
        // !prevState.downloadModalVisible })); this.buttonReset(); if
        // (this.state.downloadModalVisible === false) {

        this.setState((prevState) => ({
            downloadModalVisible: !prevState.downloadModalVisible
        }));
        // }
        console.log(
            'downloadModalVisible(handleDownloadButtonClick) : ' + this.state.downloadModalVisible
        );
        this.render();
    };

    render() {
        const {
            sortBy,
            sortAscending,
            sortNameAscending,
            download_data1,
            complete_download,
            downloadModalVisible
        } = this.state;

        console.log(
            '[render] downloadModalVisible : ' + this.state.downloadModalVisible
        );

        const sortedUserData = this
            .state
            .userData
            .slice()
            .sort((a, b) => {
                // Check if sorting by 'Name'
                if (sortBy === 'Name') {
                    const nameA = a
                        .Name
                        .toLowerCase();
                    const nameB = b
                        .Name
                        .toLowerCase();

                    // Compare names based on the sorting order
                    return sortNameAscending
                        ? nameA.localeCompare(nameB)
                        : nameB.localeCompare(nameA);
                }

                // Define the order based on color level
                const order = {
                    success: 0,
                    warning: 1,
                    danger: 2,
                    secondary: 3
                };

                // Get the color level for each user
                const levelA = this.getColorLevel(a);
                const levelB = this.getColorLevel(b);

                // Compare the color levels
                const result = order[levelA] - order[levelB];
                return sortAscending
                    ? result
                    : -result;
            });

        console.log(
            '[render][return]downloadModalVisible: ',
            this.state.downloadModalVisible
        );

        return (
            <Fragment>
                <TransitionGroup>
                    <CSSTransition
                        component="div"
                        className="TabsAnimation"
                        appear={true}
                        timeout={0}
                        enter={false}
                        exit={false}>

                        <div className='whole-page-ui'>
                            <Row>
                                <Col className='font-size-xxlarge vertical-center'>전체 사용자 보기</Col>
                                <Col className='font-size-small vertical-center horizontal-right'>Login &gt; 사용자 현황 관리 &gt; 전체 사용자 보기</Col>
                            </Row>
                            <div className='space-10px'></div>
                            <div className='boader-black'></div>
                            <div className='space-20px'></div>
                            <Row className='horizontal-right'>
                                <Col md='1' className='p-0 me-1p5'>
                                    {/* Button to toggle sorting order */}
                                    <Button
                                        color="alternate"
                                        className="font-icon-wrapper m-0 px-3 py-0"
                                        onClick={this.handleSortByName}>
                                        {
                                            this.state.sortNameAscending
                                                ? <i className='pe-7s-angle-down'></i>
                                                : <i className='pe-7s-angle-up'></i>
                                        }
                                        <p>{'이름정렬'}</p>
                                    </Button>
                                </Col>
                                <Col md='1' className='p-0  m-0'>
                                    {/* Button to toggle sorting order */}
                                    <Button
                                        color="alternate"
                                        className="font-icon-wrapper m-0 px-2 py-0"
                                        onClick={this.handleSortButtonClick}>
                                        {
                                            this.state.sortAscending
                                                ? <i className='pe-7s-angle-down'></i>
                                                : <i className='pe-7s-angle-up'></i>
                                        }
                                        <p>{'위험도정렬'}</p>
                                    </Button>
                                </Col>

                                <Col md='1'>
                                    <Link to={`../../Members/Basic/`}>
                                        <Button color="alternate" className="font-icon-wrapper m-0 px-1 py-0">
                                            <i className='pe-7s-back'></i>
                                            <p>{'돌아가기'}</p>
                                        </Button>
                                    </Link>
                                </Col>
                            </Row>
                            <Row>
                                <Col className='mb-3'>
                                    <this.CustomProgressLabels/>
                                </Col>
                            </Row>
                            <Row>
                                {
                                    sortedUserData.map(item => (
                                        <Col key={item.User_id} md={2} className="mb-3">
                                            <this.CustomCard label={item.Name} user={item}/>
                                        </Col>
                                    ))
                                }
                            </Row>
                        </div>
                    </CSSTransition>
                </TransitionGroup>
            </Fragment>
        )
    }
    getColorLevel(user) {
        // Your existing logic to determine the color level ...
        var level = 'success';

        if (user.hrData === '미측정' || user.spo2Data === '미측정' || user.hrData < 0 || user.spo2Data < 0) {
            level = 'secondary';
        }

        if ((user.hrData >= 0 && user.hrData <= 70) || (user.spo2Data >= 0 && user.spo2Data <= 94)) {
            level = 'warning';
        }

        if ((user.hrData >= 0 && (user.hrData <= 60 || user.hrData > 110)) || (user.spo2Data >= 0 && user.spo2Data <= 90)) {
            level = 'danger';
        }

        return level; // Should be one of 'success', 'warning', 'danger', 'secondary'
    }
}
export default withRouter(BasicInfomantion);
class CustomModal extends
React.Component {
    constructor(props) {
        super(props);
        this.state = {
            modal: false
        };

        this.toggle = this
            .toggle
            .bind(this);
    }

    toggle() {
        console.log('toggle called!');
        this.setState({
            modal: !this.state.modal
        });
    }

    DetailInfoButton = ({user}) => {

        var level = 'success';

        if (user.hrData === '미측정' || user.spo2Data === '미측정' || user.hrData < 0 || user.spo2Data < 0) {
            level = 'secondary';
        }

        if ((user.hrData >= 0 && user.hrData <= 70) || (user.spo2Data >= 0 && user.spo2Data <= 94)) {
            level = 'warning';
        }

        if ((user.hrData >= 0 && (user.hrData <= 60 || user.hrData > 110)) || (user.spo2Data >= 0 && user.spo2Data <= 90)) {
            level = 'danger';
        }

        return (
            <Row className='m-0'>
                <Col className='m-0 p-2'>
                    <Button
                        className="text-white"
                        style={{
                            borderColor: 'white'
                        }}
                        color={level}
                        onClick={this.toggle}>
                        요약
                    </Button>
                </Col>
                <Col className='m-0 p-2'>
                    <Link to={`../../Dashboards/basic/${user.User_id}`}>
                        <Button
                            className="text-white"
                            style={{
                                borderColor: 'white'
                            }}
                            color={level}>
                            챠트
                        </Button>
                    </Link>

                </Col>
            </Row>
        );
    };

    render() {
        const {selectedUser} = this.props;

        var hrDataBgColor = 'custom-color-success';
        var spo2DataBgColor = 'custom-color-success';
        var stepDataBgColor = 'custom-color-success';
        // var stressDataBgColor = 'custom-color-success';
        var recoveryDataBgColor = 'custom-color-success';
        var depressDataBgColor = 'custom-color-success';
        // var abnormalHrDataBgColor = 'custom-color-success'; if (selectedUser.hrData
        // === '미측정')     hrDataBgColor = 'custom-color-secondary'; else if
        // (selectedUser.hrData > 110 || selectedUser.hrData < 50)     hrDataBgColor =
        // 'custom-color-danger';

        if (selectedUser.hrData === '미측정' || selectedUser.hrData < 0) 
            hrDataBgColor = 'custom-color-secondary';
        else if (selectedUser.hrData <= 70 && selectedUser.hrData > 60) 
            hrDataBgColor = 'custom-color-warning';
        else if (selectedUser.hrData <= 60 || selectedUser.hrData > 110) 
            hrDataBgColor = 'custom-color-danger';
        
        // if (selectedUser.spo2Data === '미측정')     spo2DataBgColor =
        // 'custom-color-secondary'; else if (selectedUser.spo2Data < 80)
        // spo2DataBgColor = 'custom-color-danger';

        if (selectedUser.spo2Data === '미측정' || selectedUser.spo2Data < 0) 
            spo2DataBgColor = 'custom-color-secondary';
        else if (selectedUser.spo2Data <= 94 && selectedUser.spo2Data > 90) 
            spo2DataBgColor = 'custom-color-warning';
        else if (selectedUser.spo2Data <= 90) 
            spo2DataBgColor = 'custom-color-danger';
        
        if (selectedUser.stepData === '미측정') 
            stepDataBgColor = 'custom-color-secondary';
        else if (selectedUser.stepData === 0) 
            stepDataBgColor = 'custom-color-danger';
        
        // if (selectedUser.stressData === '미측정')     stressDataBgColor =
        // 'custom-color-secondary'; else if (selectedUser.stressData === 'Y')
        // stressDataBgColor = 'custom-color-danger';

        if (selectedUser.recoveryData === '미측정') 
            recoveryDataBgColor = 'custom-color-secondary';
        else if (selectedUser.recoveryData === '보통') 
            recoveryDataBgColor = 'custom-color-warning';
        else if (selectedUser.recoveryData === '낮음') 
            recoveryDataBgColor = 'custom-color-danger';
        
        if (selectedUser.depressData === '미측정') 
            depressDataBgColor = 'custom-color-secondary';
        else if (selectedUser.depressData === '경도') 
            depressDataBgColor = 'custom-color-warning';
        else if (selectedUser.depressData === '심각' || selectedUser.depressData === '중증도') 
            depressDataBgColor = 'custom-color-danger';
        
        // if (selectedUser.abnormalHrData === '미측정')     abnormalHrDataBgColor =
        // 'custom-color-secondary'; else if (selectedUser.abnormalHrData === 'Y')
        // abnormalHrDataBgColor = 'custom-color-danger';

        return (
            <span className="d-inline-block">
                <this.DetailInfoButton user={selectedUser}/>
                <Modal
                    isOpen={this.state.modal}
                    toggle={this.toggle}
                    className={this.props.className}
                    backdrop={true}>
                    <ModalHeader toggle={this.toggle}>{selectedUser && `${selectedUser.Name}님의 상세정보`}
                    </ModalHeader>
                    <ModalBody>
                        <Table className="mb-0" bordered={true}>
                            <tbody>
                                <tr>
                                    <td className='custom-table-color'>이름</td>
                                    <td>{selectedUser.Name}</td>
                                    <td className='custom-table-color'>아이디</td>
                                    <td>{selectedUser.Id}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>핸드폰번호</td>
                                    <td>{selectedUser.Hp_num}</td>
                                    <td className='custom-table-color'>보호자 핸드폰번호</td>
                                    <td>{selectedUser.Superv_hp_num}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>심박수</td>
                                    <td className={hrDataBgColor}>{selectedUser.hrData}</td>
                                    <td className='custom-table-color'>산소포화도</td>
                                    <td className={spo2DataBgColor}>{selectedUser.spo2Data}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>회복탄력성</td>
                                    <td className={recoveryDataBgColor}>{selectedUser.recoveryData}</td>
                                    <td className='custom-table-color'>우울증</td>
                                    <td className={depressDataBgColor}>{selectedUser.depressData}</td>
                                </tr>
                                <tr>
                                    <td className='custom-table-color'>걸음수</td>
                                    <td className={stepDataBgColor}>{selectedUser.stepData}</td>
                                    {/* <td className='custom-table-color'>스트레스</td>
                                    <td className={stressDataBgColor}>{selectedUser.stressData}</td> */
                                    }
                                </tr>
                            </tbody>
                        </Table>
                    </ModalBody>
                </Modal>
            </span>
        );
    }
}

